<template>
  <tas-base-crud :config="config" hide-create hide-delete hide-update hide-filter hide-detail>
    <template v-slot:list-table-action="data">
      <a @click="$children[0].getEvent('update', data.rowData)" class="btn btn-warning btn-sm"> Update Komponen</a>
    </template>
    <template v-slot:form-header="data">
      <template v-if="data.rowData">
        <div v-if="data.rowData.is_custom_component" class="alert alert-custom alert-light-info" role="alert">
          <div class="alert-icon">
            <span class="svg-icon svg-icon-info svg-icon-xl">
              <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                  <rect x="0" y="0" width="24" height="24" />
                  <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3" />
                  <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1" />
                  <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1" />
                </g>
              </svg>
            </span>
          </div>
          <div class="alert-text">Komponen ini merupakan <b>Komponen dengan Perhitungan Custom</b>, sehingga nilai dasar yang diinputkan akan menjadi komponen nilai dalam perhitungan sesuai dengan modul yang ditentukan </div>
        </div>
      </template>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-mapping-template-salary-employees-detail',
  data () {
    return {
      config: {
        title: 'Komponen Penggajian',
        model_api: null,
        getter: 'custom/mapping-template-salary-employees',
        setter: 'custom/mapping-template-salary-employees',
        pk_field: 'name',
        filter_api: {},
        custom_api: {},
        permission: {
          create: 'create-mapping-salary-accounts',
          read: 'view-mapping-salary-accounts',
          update: 'update-mapping-salary-accounts',
          delete: 'delete-mapping-salary-accounts'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'name', label: 'Nama Komponen', methods: { list: true, detail: true, create: true, update: true, filter: false } },
          { id: 'default_value', label: 'Nilai Dasar', methods: { list: { transform: 'money' }, detail: { transform: 'money' }, create: { type: 'money' }, update: { type: 'money' }, filter: false } },
          {
            id: 'salary_component_category_id',
            label: 'Kategori',
            methods: {
              list: { view_data: 'rel_salary_component_category_id' },
              detail: { view_data: 'rel_salary_component_category_id' },
              create: false,
              update: false,
              filter: {
                setter: 'master-salary-component-categories',
                getter: 'master-salary-component-categories',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



